import React, { useEffect, useState } from 'react';
import productList from './productList'

const Product = ({
    
}) => {

    const [quantity, setQuantity] = useState(1);
    const [cart, setCart] = useState([]);
    const [product, setProduct] = useState(); 

    useEffect(() => {
        setProduct(productList[0]); 
    }, []);
    const nextProduct = () => {
        const nextIndex = (productList.indexOf(product) + 1) % productList.length;
        setProduct(productList[nextIndex]);
    }
    const handleAddToCart = () => {
        const item = {
            ...product,
            quantity,
            total: product.price * quantity
        };
        setCart([item]); // For demo: only 1 product
    };

    const formatCurrency = (num) =>
        '$' + num.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');

    return (
        <div className='containerBox'>
            <div className='containerDetail pt-20 pb-20 contentLeft'>
                <div className='bold'>
                    {product && (
                        product.name
                    )}
                </div>
            </div>
            <div className='containerDetail bg-white'>
                {product && (
                    <img src={product.image} alt={product.name} className='containerBox bg-white width-100-percent maxWidth300'/>
                )}
            </div>
            <div className='containerDetail pt-10 pb-10 contentLeft'>
                <div className='mt-10 size15'>
                    {product && (
                        product.description
                    )}
                </div>
                <div className='mt-10 size15'>
                    {product && (
                        formatCurrency(product.price)
                    )}
                </div>
            </div>
            <div className='containerDetail p-10 bg-lite mt-10 contentLeft size15'>
                Quantity:
                <span>
                    <label>
                        <input
                            type='number'
                            value={quantity}
                            min='1'
                            onChange={(e) => setQuantity(parseInt(e.target.value))}
                            className='containerDetail width-50-percent ml-10 color-lite'
                        />
                    </label>
                </span>
            </div>
            <button onClick={handleAddToCart} className='containerBox button mt-20' >
                Add to Cart
            </button>
            <button onClick={nextProduct} className='containerBox button mt-20' >
                Next
            </button>

            {
                cart.length > 0 && (
                    <div className='containerBox'>
                        <h3>🛒 Cart</h3>
                        <p>{cart[0].name} x {cart[0].quantity}</p>
                        <strong>Total: {formatCurrency(cart[0].total)}</strong>
                    </div>
                )
            }
        </div>
    );
};

export default Product;